import axios from 'axios';
const anime = require('animejs');
import {createLoginForm} from './LoginForm.js';

let color = ['#565857', '#06D6A0'];
let button = document.querySelector('.homebutton');

export let animeButton = anime.timeline({
  autoplay: false
});

let animation_th_boolean = true;
animeButton.add({
  //#region Button text disappeard
  targets: '.homebuttontext',
  opacity: 0,
  duration: 250,
  complete: () => document.querySelector('.homebuttontext').remove()
  //#endregion
}).add({
  //#region Button => Kreis & fill w/ bordercolor
  targets: '.homebutton',
  width: '10vh',
  borderRadius: '5vh',
  backgroundColor: color[0],
  duration: 500,
  easing: 'linear',
  update: (anim) =>{
      button.style.border = 'solid ' + 2*(-anim.progress+100)/100 + ' ' + color[0] + ' ';
    }
  //#endregion
})
.add({
  //#region translate to top right corner & change backgroundColor
  targets: '.homebutton',
  translateX: '' + ((window.innerWidth/2)-65) + '',
  translateY: '' + ((-window.innerHeight/2)+70) + '',
  easing: 'easeInOutExpo',
  duration: 1000,
  update: (anim) => {
    if((anim.progress > 60) && animation_th_boolean){
        animation_th_boolean = false;
        anime({
          targets: '.homebutton',
          opacity: 0,
          duration: 30,
        });
        anime({
          targets: 'body',
          backgroundColor: color[0],
          duration: 300});
    }
  }
  //#endregion
})
.add({
   //#region change circleColor and Translate back
  targets: '.homebutton',
  backgroundColor: color[1],
  opacity:  1,
  translateX: [((window.innerWidth/2)-65), 0],
  translateY: [((-window.innerHeight/2)+70),0],
  easing: 'easeInOutExpo',
  duration: 1000
  //#endregion
 })
.add({
    //#region circle => button
  targets: '.homebutton',
  width: '20vw',
  borderRadius: '7.5rem',
  border: 'none',
  backgroundColor: color[1],
  easing: 'linear',
  duration: 500
  //#endregion
 })
.add({
    //#region button => formular
  targets: '.homebutton',
  height: '60vh',
  easing: 'linear',
  duration: 500,
  complete: () =>{
    button.classList.add('loginForm');
    createLoginForm();
  }
//#endregion
});

