let button = document.querySelector('.homebutton');
export const createLoginForm = () =>{
    let form = document.createElement("form");
    form.classList.add('loginForm');
    createNode('input', 'email', 'email', 'max.mustermann@gmx.at', true, form);
    createNode('input', 'password', 'password', '', true, form);
    createNode('input', 'submit', 'submit', 'send',false, form);
    button.appendChild(form);
    form.childNodes[2].addEventListener('click', send);
}
const send = e =>{
    e.preventDefault();
}


const createNode = (nodeType, formType, name, value, required, parent ) =>{
    let node = document.createElement("" + nodeType + "");
    node.type = formType;
    node.name =  name;
    node.value = value;
    node.required = required;
    parent.appendChild(node);
}